'use client'

export interface FormInput {
    type: string,
    placeholder: string,
    label: string,
    onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void,
    value?: string,
    errorMessage?: string,
    ref?: any
}

export const FormInput = ({type, placeholder, label, onChange, value, errorMessage, ref}: FormInput) => {
    return (
        <label className="flex flex-col gap-[2px] font-normal">
            <span className="font-light text-gray-400 text-sm">
                {label}
            </span>
            <input className="text-black-400 p-0.5 w-70 h-10 border-1 border-blue-300 rounded pl-2" ref={ref} type={type} placeholder={placeholder} value={value} onChange={onChange}/>
            {errorMessage}
        </label>
    )
}