'use client'

import { z } from "zod"
import { Button } from "../ui/Button/Button"
import { FormInput } from "../ui/FormInput/FormInput"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod";
import { user } from "@/api/user"
import Link from "next/link"
import { FormEvent, useState } from "react"
import { api } from "@/pages/api/api"
import CONFIG from '@/pages/api/config'
import handler from "@/pages/api/config"

const authFormSchema = z.object({
        email: z.string().email(),
        password: z.string(),
    })

type AuthFormType = z.infer<typeof authFormSchema>

export const AuthForm = () => {

    const [email, setEmail] = useState<string>('')
    const [password, setPassword] = useState<string>('')
    const [errorMessageEmail, setErrorMessageEmail] = useState<string>('')
    
    // const {register, handleSubmit, formState: { errors }} = useForm<AuthFormType>({
    //     resolver: zodResolver(authFormSchema)
    // })

    const handleClickAuth = (e: FormEvent<HTMLFormElement>) => {
        if(email.length === 0 || email === null || email === undefined) {
            setErrorMessageEmail('Введен некорректный логин')
        }

        e.preventDefault()
        api('auth', {email, password}, CONFIG)
    }

    return (
        <>
            <form onSubmit={(e) => handleClickAuth(e)} name="authForm" className="flex flex-col gap-[16px] items-center justify-center border-1 border-sky-300 pt-12 pb-12 pl-10 pr-10 rounded-[20px] shadow-md">
                <h2 className="font-medium text-lg m-0">Авторизация</h2>
                <FormInput placeholder="E-mail" type="text" label="E-mail" value={email} onChange={(e) => setEmail(e.target.value)} errorMessage={errorMessageEmail}/>
                <FormInput placeholder="Пароль" type="password" label="Пароль" value={password} onChange={(e) => setPassword(e.target.value)}/>
                <Button classname={"font-medium bg-blue-500 pl-7 pr-7 pt-2 pb-2 rounded flex justify-center items-center text-white cursor-pointer"} type="submit">
                    Отправить
                </Button>
                <Link href={'/register'}>
                    <Button classname="cursor-pointer">
                        Регистрация
                    </Button>
                </Link>
            </form>
        </>
    )
}